$(".filter-item__show").click(function () {
    if($(this).parents(".filter-item").hasClass("_open")){
        $(".filter-item").removeClass("_open")
    } else{
        $(".filter-item").removeClass("_open")
        $(this).parents(".filter-item").addClass("_open")
    }
})

$(document).mouseup( function(e){
    let div = $( ".filter-item" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_open")){
            $(div).removeClass("_open")
        }
    }
});

let serviceFilter = []
let metroFilter = []

let searchSelect = $('#search-select').selectize({
    respect_word_boundaries: false,
})
let control = searchSelect[0].selectize

$('[name="sort"]').change(function () {
    $(".filter-item").removeClass("_open")
})

$('.filter-select').change(function () {
    let selectedOption = $(this).find("option:selected")
    metroFilter = []
    metroFilter.push({
        id: $(selectedOption).attr("data-id"),
        value: $(selectedOption).attr("value"),
    })
    setSelectedFilter()
    renderFilters()
})

$('[name="service"],[name="metro"]').change(function () {
    setSelectedFilter()
    renderFilters()
    $(".filter-item").removeClass("_open")
})

function setSelectedFilter() {
    serviceFilter = []

    let serviceChecked = $('[name="service"]:checked')
    $(serviceChecked).each(function (i,el) {
        serviceFilter.push({
            id: $(el).attr("data-id"),
            value: $(el).attr("data-value"),
        })
    })

    console.log(serviceFilter)
}

function renderFilters() {
    $(".selected-filters").html("")
    serviceFilter.forEach(el => {
        $(".selected-filters").append(`
        <div data-id="${el.id}" class="selected-filter _type">
            <span class="text14">${el.value}</span>
            <button class="selected-filter__remove">
                <img src="img/close2.svg" alt="">
            </button>
        </div>
        `)
    })
    metroFilter.forEach(el => {
        $(".selected-filters").append(`
        <div data-id="${el.id}" class="selected-filter _metro">
            <span class="text14">${el.value}</span>
            <button class="selected-filter__remove">
                <img src="img/close2.svg" alt="">
            </button>
        </div>
        `)
    })

    $("._type .selected-filter__remove").click(function () {
        let id = $(this).parents(".selected-filter").attr("data-id")
        $(`[data-id="${id}"]`).prop("checked",false)
        setSelectedFilter()
        renderFilters()
    })

    $("._metro .selected-filter__remove").click(function () {
        control.clear();

        metroFilter = []
        setSelectedFilter()
        renderFilters()
        console.log(true)
    })
}

setSelectedFilter()
renderFilters()
